.card {
  display: grid;
  justify-content: center;
  max-width: 350px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  padding: 35px;
  background-color: #fff;
  border: 1px solid #bfbfbf;
  border-radius: 8px;
  box-shadow: 0px 0px 8px 0px rgba(66, 68, 90, 0.35);
}

.title {
  display: block;
  margin-bottom: 15px;
  margin-top: 0px;
  text-align: center;
  font-size: 20px;
  color: #4a4a4a;
}
