.name {
  display: grid;
  justify-content: center;
  align-content: center;
  margin-bottom: 5px;
  font-size: 20px;
  color: #4a4a4a;
}

.input {
  display: grid;
  width: 250px;
  height: 22px;
  padding: 5px;
  margin-top: 5px;
  margin-bottom: 10px;
  border-radius: 25px;
  border-width: 1px;
  border-color: #f7d373;
}

input:focus,
input:hover {
  outline: none;
}

@media screen and (max-width: 350px) {
  .input {
    width: 230px;
  }
}
